body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {

}

.lineaTiempo{
  border-bottom: 1px solid black;
  display: inline-block;
  width: 65%;
  position: relative;
}
.contenedorTiempo{
  text-align: center;
}
.fechasLinea{
  padding: 15px;
  border: 1px solid black;
  width: 50%;
  margin: 10px auto;
}
.fechasLinea input{
  width: 5em;
  margin: 3px;
}
.fechasLinea div{
  text-align: center;
}

.formAdd h1{
  font-size: 25px;
}
.formAdd{
  padding: 8px;
  border: 1px solid black;
  margin: 15px auto;
  width: 80%;
  margin-bottom: 250px;
}
.formAdd input, .formAdd label{
  display: inline-block;
  width: 50%;
}
.formAdd input{
  padding: 5px;
  outline: none;
}
.formAdd button{
  display: block;
  margin: 5px auto;
}
.espacioTrabajo{
  margin-bottom: 50px;
}
.espacioTrabajo span{
  margin: 0 15px;
  border-bottom: 1px solid black;
}

.elemento{
  position: absolute;
  bottom: -8px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.elemento .contenido{
  height: 200px;
  border: 1px solid black;
  background: white;
  margin-bottom: -7px;
  width: 130px;
}
.elemento img{
  width: 100%;
}
.elemento .contenido h6{
  margin: 0;
  font-size: 14px;
}
.elemento .contenido .fechaElemento{
  display: block;
  border: none;
  font-size: 18px;
  margin-bottom: 15px;
}
.elemento i{
  font-size: 10px;
  transition: all 0.4s ease;
  cursor: pointer;
  order: 1;
}
.elemento i:hover{
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}

.elemento .contenido button{
  border: none;
  outline:none;
  background: lightblue;
  border: 1px solid black;
  padding: 5px;
  cursor:pointer;
  transition: all 0.4s ease;
}
.elemento .contenido button:hover{
  background: lightslategray;
  color: white;
}

